
@charset "UTF-8";
.food {
  position: fixed;
  left: 0;
  top: 0;
  bottom: 48px;
  z-index: 9;
  width: 100%;
  background: #fff;
  transition: all 0.2s linear;
  -webkit-transform: translate3d(0, 0, 0);
          transform: translate3d(0, 0, 0);
}
.food.move-enter, .food.move-leave-active {
    -webkit-transform: translate3d(100%, 0, 0);
            transform: translate3d(100%, 0, 0);
}
.food .img-header {
    position: relative;
    width: 100%;
    height: 0;
    padding-top: 100%;
}
.food .img-header img {
      position: absolute;
      top: 0;
      left: 0;
      width: 100%;
      height: 100%;
}
.food .img-header .back {
      position: absolute;
      top: 10px;
      /*需要处理的样式*/
      left: 10px;
      width: 40px;
      height: 40px;
      border-radius: 50%;
      background: rgba(0, 0, 0, 0.7);
}
.food .img-header .back .icon-arrow_lift {
        display: block;
        padding: 10px;
        font-size: 20px;
        color: #fff;
}
.food .content {
    position: relative;
    padding: 18px;
}
.food .content .title {
      line-height: 14px;
      margin-bottom: 8px;
      font-size: 14px;
      font-weight: 700;
      color: #07111b;
}
.food .content .detail {
      margin-bottom: 18px;
      line-height: 10px;
      font-size: 0;
}
.food .content .detail .sell-count,
      .food .content .detail .rating {
        font-size: 10px;
        color: #93999f;
}
.food .content .detail .sell-count {
        margin-right: 12px;
}
.food .content .price {
      font-weight: 700;
      line-height: 24px;
}
.food .content .price .now {
        margin-right: 8px;
        font-size: 14px;
        color: #f01414;
}
.food .content .price .old {
        text-decoration: line-through;
        font-size: 10px;
        color: #93999f;
}
.food .cartcontrol-wrapper {
    position: absolute;
    right: 12px;
    bottom: 12px;
}
.food .buy {
    position: absolute;
    right: 18px;
    bottom: 18px;
    z-index: 10;
    height: 24px;
    line-height: 24px;
    padding: 0 12px;
    box-sizing: border-box;
    font-size: 10px;
    border-radius: 12px;
    color: #fff;
    background: #00a0dc;
    transition: all 0.2s;
    opacity: 1;
}
.food .buy.fade-enter, .food .buy.fade-leave-active {
      opacity: 0;
}
.food .info {
    padding: 18px;
}
.food .info h1.title {
      line-height: 14px;
      margin-bottom: 16px;
      font-size: 14px;
      color: #07111b;
}
.food .info .text {
      line-height: 24px;
      padding: 0 8px;
      font-size: 12px;
      color: #4d555d;
}
.food .rating {
    padding-top: 18px;
}
.food .rating .title {
      line-height: 14px;
      margin-left: 16px;
      font-size: 14px;
      color: #07111b;
}
.food .rating .rating-wrapper {
      padding: 0 18px;
}
.food .rating .rating-wrapper .rating-item {
        position: relative;
        padding: 16px 0;
        position: relative;
}
.food .rating .rating-wrapper .rating-item:after {
          display: block;
          position: absolute;
          left: 0;
          bottom: 0;
          width: 100%;
          border-top: 1px solid rgba(7, 17, 27, 0.1);
          content: " ";
}
.food .rating .rating-wrapper .rating-item .user {
          position: absolute;
          right: 0;
          top: 16px;
          line-height: 12px;
          font-size: 0;
}
.food .rating .rating-wrapper .rating-item .user .name {
            display: inline-block;
            margin-right: 6px;
            vertical-align: top;
            font-size: 10px;
            color: #93999f;
}
.food .rating .rating-wrapper .rating-item .user .avatar {
            border-radius: 50%;
}
.food .rating .rating-wrapper .rating-item .time {
          margin-bottom: 6px;
          line-height: 12px;
          font-size: 10px;
          color: #93999f;
}
.food .rating .rating-wrapper .rating-item .text {
          line-height: 16px;
          font-size: 12px;
          color: #07111b;
}
.food .rating .rating-wrapper .rating-item .text .icon-thumb_up, .food .rating .rating-wrapper .rating-item .text .icon-thumb_down {
            margin-right: 4px;
            line-height: 16px;
}
.food .rating .rating-wrapper .rating-item .text .icon-thumb_up {
            color: #00a0dc;
}
.food .rating .rating-wrapper .rating-item .text .icon-thumb_down {
            color: #93999f;
}
.food .rating .no-rating {
      padding: 16px 0;
      font-size: 12px;
      color: #93999f;
}



